import styled from "styled-components";
import React from "react";
import CardSymbol from "./Atomics/CardSymbol";
import NavButton from "./Atomics/NavButton";

const Card = styled.div`
    width: 200px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    overflow: hidden;
    flex-grow: 0;
    flex-shrink: 0;
`;

const CardTitle = styled.h1`
    padding: 8px 8px 8px 8px;
    font-size: 13pt;
    color: #666;
`;

const CardDescription = styled.div`
    padding: 0 8px 8px 8px;
    font-size: 11pt;
`;

const CardLink = styled(NavButton)`
    font-size: 10pt;
    padding: 8px;
    color: #0d47a1;
`;

const CardWrapper = styled.div`
    padding: 8px;
`;

const CourseCard = ({symbol, children, to, title, ...props}) => {
    return (
        <CardWrapper>
            <Card>
                <CardSymbol>{symbol}</CardSymbol>
                <CardTitle>{title}</CardTitle>
                <CardDescription>{children}</CardDescription>
                <CardLink text to={to}>Zum Kurs</CardLink>
            </Card>
        </CardWrapper>
    )
}

export default CourseCard;