import React from 'react';
import {useParams} from 'react-router-dom';
import {useGetExercises} from "../../Providers/CourseProvider";
import CourseCard from "../../CourseCard";
import Section from "../../Atomics/Section";
import CourseCarousel from "../../CourseCarousel";

const Module = () => {
    const {courseId, moduleGroupId, moduleId} = useParams();
    const exercises = useGetExercises(courseId, moduleGroupId, moduleId);

    const constructExercises = () => {
        const temp = [];

        for(let key in exercises) {
            const {title, description, symbol} = exercises[key];

            temp.push(
                <CourseCard key={key} title={title} symbol={symbol} to={`/course/${courseId}/${moduleGroupId}/${moduleId}/${key}`}>
                    {description}
                </CourseCard>
            )
        };

        return temp;
    };

    return (
        <Section title={'ModulÃ¼bersicht'}>
            <CourseCarousel>
                {constructExercises()}
            </CourseCarousel>
        </Section>
    );
};

export default Module;