import styled from "styled-components";
import React from "react";

const Base = styled.section`
    border-bottom: 1px solid #ccc;
    padding-bottom: 8px;
    margin-bottom: 16px;
    
    + :last-child {
        border: none;
    }
`;

const Title = styled.div`
    font-size: 16pt;
    margin-bottom: 16px;
    color: black;
`;

const Section = ({title, children, ...props}) => {
    return (
        <Base {...props}>
            <Title>{title}</Title>
            {children}
        </Base>
    )
};

export default Section;